// 侧边栏通用
.sidebar {
  width: $side-bar-width;
  float: left;
}

.widget {
  margin-bottom: $separation;
  background-color: #fff;
  @include box-shadow-normal;
  li {
    padding: 10px 0;
  }
  h3 {
    padding: 10px 15px;
    border-bottom: 1px solid #f2f5ff;
    font-size: 16px;
  }
  ul {
    padding: 15px;
  }
  .widget-inner {
    padding: 15px;
    line-height: 1.8;
  }
  .more {
    font-size: 12px;
    font-weight: normal;
    float: right;
  }
  .info {
    color: $color-text-3;
  }
}

// 侧边栏微语
.widget_twitter {
  .info {
    margin-top: 10px;
  }
}

// 侧边栏最新评论
.widget-comment {
  .comment-inner {
    display: flex;
    .avatar {
      width: 40px;
      height: 40px;
      flex: 0 0 40px;
      margin-right: $separation;
    }
    .username {
      font-style: normal;
      font-weight: bold;
    }
    .time {
      float: right;
      color: $color-text-3;
    }
    .widget-comment-content {
      flex: 1;
    }
    .comment-content {
      color: $color-text-2;
    }
  }
  .comment-refer {
    margin: 10px 0;
    .iconfont {
      font-size: 22px;
      color: $light-grey;
      margin-right: 10px;
    }
    a {
      margin-left: 10px;
    }
  }
}

// 两列文章列表
.widget-list-two-columns {
  display: flex;
  flex-wrap: wrap;
  li {
    flex: 0 0 50%;
    padding-top: 0;
  }
}

// 带图片的侧边栏文章列表
.widget-list-media {
  li {
    display: flex;
    height: 80px;
  }
  .img, .right {
    display: block;
    flex: 1;
  }
  img {
    width: 100px;
    max-height: 68px;
    border-radius: 4px;
  }
  .img {
    flex: 0 0 110px;
    &:hover {
      opacity: .8;
    }
  }
  h4 {
    overflow: hidden;
    margin-bottom: 0.5em;
  }
}

// 有序号的侧边栏文章列表
.widget-list-top {
  li {
    display: flex;
  }
  a {
    flex: 1;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  .index {
    display: inline-block;
    flex: 0 0 20px;
    height: 18px;
    line-height: 18px;
    background: #ffe4c4;
    margin-right: $separation;
    text-align: center;
    color: #ff8200;
    margin-top: 3px;
    border-radius: 4px;
  }
  .top-0, .top-1, .top-2 {
    font-weight: bold;
    background: #FF4949;
    color: #fff;
  }
  .view {
    flex: 0 0 65px;
    text-align: right;
  }
}

// 侧边栏分类
.widget-sorts {
  li {
    &.active,
    &:hover {
      transition: all .3s;
      color: $primary;
      font-weight: bold;
    }
  }
}

$avatarWidth: 86px;
// 侧边栏用户
.widget-user {
  background-size: 100% 130px;
  text-align: center;
  padding-top: 130px - $avatarWidth / 2 - 4px;
  position: relative;
  &:after {
    position: absolute;
    top: 0;
    left: 0;
    content: '';
    display: block;
    width: 100%;
    height: 130px;
    background: $page-background url("../images/shan.png") no-repeat;
    z-index: 0;
    filter: brightness(0.8);
  }
  .username {
    font-size: 22px;
    display: inline-block;
    margin-bottom: 0.5em;
  }
  .desc {
    position: relative;
    padding: 0 $padding $padding;
    z-index: 1;
    p {
      text-align: justify;
      margin-top: 0.5em;
      color: $color-text-2;
    }
  }
  .avatar {
    width: $avatarWidth;
    height: $avatarWidth;
    line-height: $avatarWidth - 12px;
    background-color: rgba(255, 255, 255, .8);
    border-radius: 50%;
    font-size: 38px;
    margin: 0 auto;
    transition: transform .4s;
    box-sizing: border-box;
    padding: 6px;
    &:hover {
      transform: rotate(360deg);
    }
  }
  .iconfont {
    font-size: 20px;
    margin: 0 5px;
  }
}

// 侧边栏日历
#calendar {
  .day, .day2 {
    position: relative;
    &:after {
      position: absolute;
      top: -10px;
      left: 15px;
      width: max-content;
      font-size: 12px;
      transform: scale(.5);
      padding: 3px 5px;
      border-radius: 13px;
      color: #fff;
    }
  }
  .calendartop {
    width: 100%;
    text-align: center;
  }

  .calendartop td {
    width: 50%;
    padding: 16px 0;
  }

  .calendar {
    width: 100%;
    padding: 0 20px 20px;
  }

  .calendar td {
    text-align: center;
    padding: 5px 0;
  }

  .calendar td a {
    font-weight: bold;
    display: block;
  }
  .day {
    background-color: $page-background;
    &:after {
      content: '今日';
      background: red;
    }
  }
  .day2 {
    background: $page-background;
    &:after {
      content: '有更新';
      background: #03A9F4;
    }
  }
}

.widget-visitor {
  img {
    width: 100%;
  }
}