$border-radius: 6px;
$tools-item-width: 50px;

$searchHeight: 50px;

// 内容区域宽度
$content-width: 865px;
// 边栏宽度
$side-bar-width: 330px;
// 通用间距
$separation: 15px;
// 通用内边距
$padding: 20px;

/**
 * 颜色变量
 * 说明：
 *  1. 按照注释，了解每个颜色的大致用途
 *  2. chrome开发工具，找到要修改颜色的选择器
 *  3. 在src/css目录中，找到该选择器，并最终确定该元素用的那个颜色。
 *  4. 可直接使用本文件中的颜色替换该颜色，也可以在本文件中修改相应颜色。
 *  5. 修改本文件中的颜色，可能影响的不止一处，需慎重考虑。
 *  6. 如仅需修改那一处颜色，可直接在scss文件中填入目标颜色，而不使用本文件中的变量。
 *  7. 建议使用变量，可使页面颜色可控、统一。
 *  8. 修改后，需在命令行运行 gulp build
 *  9. 可先百度搜索gulp使用教程
 */

/**
 * 主色调
 */
$primary: #3e64ff;

/**
 * 主色调加深, 主要跟主色调产生对比
 */
$dark-primary: darken($primary, 10%);

/**
 * 主色调变浅
 */
$light-primary: lighten($primary, 10%);

/**
 * 页面的背景颜色
 */
$page-background: #f7f9ff;

/**
 * 页面文字颜色
 */
$color-text-1: #000;
$color-text-2: #545454;
$color-text-3: #aaacb6;

/**
 * black
 * 用于footer背景，及某些字体颜色
 * 1.按钮背景 2.文本默认颜色
 */
$black: #111;
$light-black: #333;
$dark-black: #000;

/**
 * 用于警告或重点提示
 */
$red: #FF4949;
$danger: #FF4949;
$warning: #ff9322;



/**
 * 标签背景
 */
$extra-light-black: #5a5a5a;

/**
 * 默认背景色
 */
$white: #fff;

$dark-white: #fff;

/**
 * 多用于边框颜色
 */
$extra-light-grey: #EFF2F7;

/**
 * 一些不太显眼的字体颜色
 */
$sliver: #8492A6;

/**
 * 多用于链接的hover颜色
 */
$blue: $primary;

/**
 * 深颜色边框
 */
$light-grey: #E5E9F2;

$box-shadow: 4px 5px 6px;