/* 导航栏 */
.nav {
  background-color: #fff;
  color: $light-black;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 999;
  padding-left: 15px;
  box-sizing: border-box;
  padding-right: 15px;
  .icon-menu {
    display: none;
  }
  // 修复导航栏由于内部的float而坍塌
  &:after {
    @include clearfix;
  }
  .logo {
    float: left;
    line-height: 60px;
    font-size: 20px;
    font-weight: bold;
    img {
      vertical-align: middle;
      margin-right: 10px;
      height: 45px;
    }
  }
}

// 导航条
.menu {
  line-height: 60px;
}
.menu-item {
  float: left;
  position: relative;
  a {
    display: block;
    padding-left: 12px;
    padding-right: 12px;
    white-space: nowrap;
  }
  span {
    position: relative;
    display: block;
  }
  &.active > a {
    color: $primary;
  }
  &:hover .sub-menu,
  a:focus + .sub-menu {
    display: block;
    min-width: 100%;
  }

  .sub-menu {
    position: absolute;
    right: 0;
    z-index: 999;
    background-color: #fff;
    line-height: 2.5;
    border-radius: 4px;
    overflow: hidden;
    display: none;
    @include box-shadow-normal;
    border: 1px solid rgba(146,146,146,0.21);
    a:hover {
      background-color: #f3f4f6;
    }
  }
  &.write-button {
    margin-left: 2em;
  }
}
.sub-nav {
  padding: 10px 0;
  border-top: 1px solid #eee;
  li {
    display: inline-block;
    margin-right: 1em;
  }
  a:hover {
    color: $primary;
  }
  .sort-list {
  }
}

.social-media {
  .icon-rss {
    color: $primary;
  }
  .icon-weixin {
    color: #5cb85c;
  }
  .icon-weibo {
    color: #FF4949;
  }
  .icon-email {
    color: #00a0d2;
  }
  .icon-qq {
    color: #ff0000;
  }
}