@media all and (max-width: 960px) {
  body {
    padding-top: 62px;
  }
  .container, .content-wrap {
    max-width: 100%;
  }
  .container {
    padding: 0;
  }
  .main {
    margin-top: 0;
  }
  .slider .swiper-container {
    height: 4rem;
  }
  .nav {
    .icon-menu{
      display: block;
      position: absolute;
      right: 15px;
      top: 21px;
    }
    .logo {
      font-size: 0.3rem;
    }
  }
  .menu {
    position: fixed;
    right: 0;
    top: 50px;
    display: none;
    width: 61.8%;
    box-sizing: border-box;
    background-color: #fff;
    z-index: 997;
    line-height: 40px;
    overflow-y: auto;
    @include box-shadow-normal;
    li {
      float: none;
    }
    .write-button {
      margin-left: 0;
    }
  }
  .menu-item {
    .sub-menu {
      position: initial;
      display: block;
      width: 100%;
      box-shadow: none;
      border-left: none;
      border-right: none;
      border-radius: 0;
    }
    span:after {
      display: none;
    }
  }
  .sub-nav {
    display: none;
  }
  .edit {
    display: none;
  }
  .header {
    height: auto;
    padding: 20px;
    .avatar {
      margin-top: 0;
      margin-right: 20px;
    }
    .username {
      padding-top: 0;
    }
  }
  .search {
    width: 100%;
    background: #eee;
    border-color: #fff;
  }
  .search-input {
    height: 40px;
    color: #333;
  }
  .content {
    float: none;
    width: 100%;
  }
  .sidebar {
    display: none;
  }
  .footer {
    display: none;
    .widget {
      width: 100%;
      padding: 0;
    }
  }
  .copyright {
    margin-top: 15px;
  }
  .site-tools {
    bottom: 10px;
    right: 10px;
    margin-bottom: 0;
    .item {
      width: 1rem;
      height: 1rem;
      line-height: 1rem;
    }
  }
  .neighbor {
    .prev, .next {
      width: 100%;
      float: none;
      margin-bottom: 10px;
    }
    .iconfont {
      display: none;
    }
  }
  .log-list-item {
    padding: 10px;
    &.log-list-image-1 {
      display: block;
      .cover-image {
        width: 100%;
      }
      .log-list-content {
        margin-top: 15px;
      }
      .description {
        min-height: auto;
      }
    }
    .info a.user {
      display: none;
    }
    .log-list-image {
      left: 15px;
    }
    .log-list-image img {
      width: 64px;
      height: 64px;
    }
    .title {
      font-size: 16px;
    }
  }
  .log-list_mv .log-list-item,
  .log-list_img .log-list-item,
  .log-list_2 .log-list-item {
    width: 100%;
    height: auto;
    padding: 15px;
    .info {
      text-align: left;
      .fr {
        display: inline-block;
      }
    }
  }
  .log-list_mv .log-list-item .log-list-image .play {
    display: block;
  }
  .log-list_text {
    list-style: none!important;
    line-height: 1.5;
    padding-top: 10px;
    padding-bottom: 10px;
    height: auto;
  }
  .log-list_text .date {
    float: none;
    display: block;
  }
  .log-list_text_title {
    padding-left: 15px;
    font-size: 26px;
  }
  .log-title {
    font-size: 16px;
    text-align: left;
    padding: 0;
  }
  .log-info {
    text-align: left;
    font-size: 12px;
    padding: 3px 0;
    span {
      margin-right: 10px;
    }
  }
  .form-group .btn {
    width: 100%;
  }
  .col-md-4 {
    float: none;
    width: 100%;
  }
  .panel-heading {
    padding-left: 15px;
  }
  .panel-body {
    padding: 15px;
  }
  .log-body h2 {
    padding: 5px 15px 5px 12px;
    left: -15px;
  }
  .relation-log li {
    width: 100%;
  }
  .search-list {
    padding: 15px;
  }
  .search-list-item {
    height: auto;
    white-space: initial;
    .pull-right {
      display: none;
    }
  }
  .user-list li {
    width: 33.33%;
  }
}
