//搜索栏
.search {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  z-index: 1000;
  display: none;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, .2);
}
.search-input {
  position: absolute;
  top: 180px;
  left: 0;
  right: 0;
  bottom: 0;
  margin: 0 auto;
  width: 800px;
  height: $searchHeight;
  padding: 10px 16px;
  font-size: 15px;
  background-color: #fff;
  box-sizing: border-box;
  border-radius: 6px;
  box-shadow: 0 0 0 10px #eee;
  border: none;
  &:focus {
    outline: none;
  }
}

// 搜索结果页面，文章列表
.search-list {
  background: #fff;
  padding: 30px;
  h1 {
    font-size: 28px;
    margin-bottom: 1em;
    font-weight: bold;
  }
}
.search-list-item {
  border-bottom: 1px dashed #eee;
  padding: 5px 0;
  height: 28px;
  white-space: nowrap;
  .pull-right .date {
    font-family: serif;
    margin-left: 1em;
  }
  .key {
    color: $primary;
    margin: 0 3px;
  }
}