html, body, h1, h2, h3, h4, h5, h6, p, img, ul, li, ol {
  margin: 0;
  padding: 0;
}

body {
  font-size: 14px;
  line-height: 1.5;
  font-family: "PingFang SC","Lantinghei SC","Microsoft YaHei","HanHei SC","Helvetica Neue","Open Sans",Arial,"Hiragino Sans GB",STHeiti,"WenQuanYi Micro Hei",SimSun,sans-serif;
  -webkit-font-smoothing: antialiased;
  background-color: $page-background;
  color: $light-black;
  overflow-x: hidden;
  padding-top: 108px;
}

img {
  border: none;
}

a {
  color: inherit;
  text-decoration: none;
  &:hover {
    color: $primary;
  }
}

li {
  list-style-type: none;
}

input, textarea {
  appearance: none;
  font-size: inherit;
}
#browsehappy {
  text-align:center;
  background:#ff0;
  padding: 10px 0;
}
#search-trigger {
  cursor: pointer;
}
.bold {
  font-weight: bold;
}
.separation {
  height: $separation;
}
.container {
  display: block;
  width: $content-width + $side-bar-width + 2 * $separation + 2 * $padding;
  padding: 0 $padding;
  box-sizing: border-box;
  max-width: 100%;
  margin: 0 auto;
}
.pull-left, .fl {
  float: left;
}
.pull-right, .fr {
  float: right;
}

.clearfix, .clear {
  clear: both;
}
.clearfix:after {
  content: '';
  display: block;
  clear: both;
}

.shadow {
  box-shadow: 0 0 5px 0 rgba(154, 154, 154, .25) !important
}

.gold-v {
  display: inline-block;
  width: 22px;
  height: 22px;
  background: url(../images/steps_gold_v.png) no-repeat;
  background-size: cover;
}

// 移动端菜单图标
.icon-menu {
  display: block;
  width: 30px;
  .icon-menu-item {
    display: block;
    height: 1px;
    background-color: $sliver;
    margin: 0 0 8px;
    &:last-of-type {
      margin-bottom: 0;
    }
  }
}

// 页头的统计信息
.count {
  overflow: hidden;
  .item {
    float: left;
    list-style-type: none;
    color: inherit;
    margin-right: 15px;
  }
}
.readmore {
  display: none;
}

.row {
  margin: -5px;
  &:after {
    display: block;
    content: '';
    overflow: hidden;
    clear:both;
  }
}
.col-md-4 {
  float: left;
  width: 33.33%;
  padding: 5px;
  box-sizing: border-box;
}

.badge {
  display: inline-block;
  line-height: 1;
  font-size: 12px;
  padding: 4px 8px;
  vertical-align: middle;
  margin-right: 5px;
  border-radius: 4px;
  font-weight: normal;
  &-danger {
    color: #f7094c;
    background: #ffe8e8;
  }
}
.log-info {
  i {
    font-style: normal;
  }
}

.copyright-notice {
  color: #000;
  background: #f2f7fb;
  padding: 10px 10px 10px 50px;
  position: relative;
  .iconfont {
    position: absolute;
    left: 10px;
    top: 10px;
    font-size: 30px;
    color: rgba(146,146,146,0.21);
  }
  a {
    text-decoration: underline;
    margin: 0 10px;
  }
}

/* 面包屑导航 */
.breadthumb {
  margin: 15px;
  .bread-item {
    color: $light-black;
    margin-right: 5px;
    padding: 0;
    &:after {
      content: ' >';
      color: #b2b2b2;
    }
    &:last-of-type:after {
      content: '';
    }
  }
}

.main {
  position: relative;
  min-height: 500px;
  margin-top: 2 * $separation;
}

.content-wrap {
  float: left;
  width: $content-width;
  box-sizing: border-box;
  margin-right: $separation;
}
.content {
  padding-bottom: $separation;
}
.content, .author {
  margin-bottom: $separation;
  @include box-shadow-normal;
}

.page-title {
  background: #fff;
  padding: 10px $padding;
  border-bottom: 1px solid darken($page-background, 1%);
  span {
    position: relative;
    display: inline-block;
    color: $primary;
    font-size: 14px;
    padding: 0 $padding;
  }
  span:after {
    position: absolute;
    bottom: -11px;
    left: 0;
    content: '';
    display: block;
    width: 100%;
    height: 2px;
    background: $primary;
  }
}

// 分页
.pagination {
  margin: 25px auto 0;
  text-align: center;
  a, span {
    display: inline-block;
    height: 28px;
    line-height: 28px;
    text-decoration: none;
    padding: 0 4px;
    box-sizing: border-box;
    margin: 0 2px;
    background-color: #f4f4f5;
    color: #606266;
    min-width: 30px;
    border-radius: 2px;
  }
  span, a:hover {
    background-color: #409eff;
    color: #fff;
  }
}
.ias-noneleft {
  clear: both;
}

//footer
.footer {
  margin-top: 30px;
  padding: 30px 0;
  background-color: #131313;
  color: #9E9E9E;
  overflow: hidden;
  .widget {
    float: left;
    width: 33.3%;
    background-color: transparent;
    padding: 0 15px;
    box-sizing: border-box;
    box-shadow: none;
    h3 {
      border-color: $light-black;
      &:after {
        display: none;
      }
    }
  }
  .topic-item {
    display: inline-block;
    margin-right: 10px;
    margin-bottom: 10px;
  }
}
.copyright {
  text-align: center;
  padding: 30px 0;
  background-color: #000;
  color: #9E9E9E;
}

.site-tools {
  position: fixed;
  bottom: 20px;
  right: 20px;
  z-index: 998;
  @include box-shadow-normal;
  .item {
    display: block;
    background-color: #fff;
    width: $tools-item-width;
    height: $tools-item-width;
    line-height: $tools-item-width;
    text-align: center;
    position: relative;
    cursor: pointer;
    &:hover, &.active {
      background-color: $light-primary;
      .iconfont {
        color: #fff;
      }
      .popup {
        display: block;
      }
    }
    .popup {
      position: absolute;
      right: 50px;
      bottom: 0;
      background: #fff;
      display: none;
      overflow: hidden;
      border: 1px solid $extra-light-grey;
      border-radius: 4px;
      @include box-shadow-normal;
      img {
        display: block;
        width: 200px;
      }
      .title {
        color: $light-black;
        line-height: 2;
        border-bottom: 1px solid #eee;
      }
    }
  }
  .gotoup {
    display: none;
  }
  .iconfont {
    font-size: 30px;
    color: $extra-light-black;
  }
}

//panel
.panel {
  background-color: #fff;
}

.panel-heading {
  padding: 10px 30px;
  font-weight: bold;
  font-size: 17px;
}
.panel-body {
  padding: 30px;
}
.content .panel {
  margin-bottom: 15px;
}

//log
.log-info {
  padding: 1em 0;
  color: $sliver;
  border-bottom: 1px solid $light-grey;
  text-align: center;
  a {
    color: $sliver;
  }
  .iconfont {
    margin-right: 5px;
  }
  span {
    margin: 0 10px;
  }
}
.log-title {
  text-align: center;
  color: $extra-light-black;
  font-size: 28px;
  font-weight: bold;
  position: relative;
  padding-right: 20px;
  padding-left: 20px;
  a {
    position: absolute;
    right: 0;
    top: 0;
  }
}
.log-body {
  padding: 30px 0;
  line-height: 2;
  font-size: 16px;
  p, strong {
    font-size: 16px;
  }
  p {
    margin-bottom: 1em;
  }
  h1, h2, h3, h4, h5, h6 {
    word-break: break-all;
  }
  h1 {
    font-size: 24px;
    text-align: center;
    margin-bottom: 1em;
  }
  h2 {
    width: 100%;
    padding: 0.2em 15px;
    background: #f3f4f6;
    margin-bottom: 1em;
    box-sizing: border-box;
    border-left: 4px solid $blue;
    font-size: 18px;
  }
  h3, h4, h5, h6 {
    font-weight: bold;
    font-size: 18px;
    margin: 0.5em 0;
  }
  strong {
    font-weight: bold;
  }
  img {
    max-width: 100%;
    height: auto;
  }
  pre {
    white-space: pre-wrap;
  }
    ol, ul {
    margin-bottom: 1em;
  }
  li {
    list-style-position: inside;
    list-style-type: circle;
    line-height: 2;
    p {
      display: inline;
    }
  }
  .text-block {
    background-color:#f3f4f6;
    padding: 10px 15px;
    text-align: center;
    margin: 1em 0;
  }
  blockquote {
    padding: 10px 20px;
    margin: 1em 0;
    color: #819198;
    border-left: 5px solid #dce6f0;
    background: #f2f7fb;
  }
  table {
    border: 1px solid $light-grey;
    border-collapse: collapse;
    th, td {
      border: 1px solid $light-grey;
      padding: 5px 10px;
    }
    tr:first-of-type {
      background-color: $light-grey;
      th, td {
        border-color: darken($light-grey, 5%);
      }
    }
  }
  video {
    display: block;
    margin: 2em auto;
  }
}
.relief {
  color: $extra-light-black;
}

#related_log, .relation {
  line-height: 1.8;
  p{
    margin-bottom: 0;
  }
  .time {
    display: inline-block;
  }
}

//neighbor
.neighbor {
  border-top: 1px solid $extra-light-grey;
  border-bottom: 1px solid $extra-light-grey;
  &:after {
    content: '';
    display: block;
    clear: both;
  }
  .prev {
    border-right: 1px solid #e5e9f2;
  }
  .prev, .next {
    width: 50%;
    float: left;
    padding: 0 10px;
    box-sizing: border-box;
    text-align: center;
  }
}

//评论
.comment-box .comment {
  padding: 20px 0 20px 68px;
  border-bottom: 1px #f7f7f7 solid;
  overflow: hidden;
  height: 100%;
  zoom: 1;
  position: relative;
  width: 100%;
  box-sizing: border-box;
}
.comment-box .comment .avatar {
  position: absolute;
  left: 0;
}
.comment-box .comment .avatar img {
  width: 48px;
  height: 48px;
}
.comment-box .comment-children {
  background-color: $dark-white;
  padding-top: 10px;
  padding-bottom: 10px;
}
.comment-box .comment-children .avatar {
  left: 10px;
}
.comment-box .comment .comment-time {
  color: $sliver;
  display: inline;
}
.comment-box .comment .comment-content {
  margin: 10px 0;
}
.comment-info #comment-post {
  width: 90%;
}

// 打赏图片
.i-reward {
  position: absolute;
  right: 15px;
  top: 15px;
}

.relation-log {
  h3 {
    padding: 15px 0 8px;
    border-bottom: 1px solid #eee;
    font-weight: bold;
    margin-bottom: 15px;
  }
  ul {
    font-size: 0;
  }
  li {
    width: 50%;
    font-size: 14px;
    display: inline-block;
    line-height: 1.8;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}
.layer-reward {
  cursor: pointer;
  &:hover {
    color: $primary;
  }
}
// 分类描述
.sort-desc {
  padding: $padding;
  border-left: 5px solid #2196F3;
  margin-bottom: 15px;
  background: #fff;
  @include box-shadow-normal;
  h4 {
    font-size: 18px;
  }
}

.user-list {
  font-size: 0;
  li {
    display: inline-block;
    text-align: center;
    padding: $padding;
    width: 12.5%;
    box-sizing: border-box;
  }
  img {
    width: 64px;
    height: 64px;
  }
  .name {
    font-size: 12px;
    white-space: nowrap;
    margin-top: 10px;
  }
}