// 首页幻灯片
.slider {
  margin-bottom: $separation;
  @include box-shadow-normal;
  .swiper-container {
    height: 420px;
  }
  .swiper-slide {
    position: relative;
    background-size: cover;
    box-sizing: border-box;
  }
  .box {
    display: block;
    height:100%;
    background: linear-gradient(0, rgba(0, 0, 0, .4), transparent);
  }
  a {
    display: block;
    height: 100%;
  }
  .title {
    position: absolute;
    bottom: 0;
    left: 0;
    padding: $padding;
    width: 100%;
    box-sizing: border-box;
    text-shadow: 0 0 1px #333;
    color: #e5e9f2;
  }
  .swiper-pagination {
    text-align: right;
    box-sizing: border-box;
    padding-right: 20px;
  }
  .swiper-pagination-bullet {
    transition: width .3s;
  }
  .swiper-pagination-bullet-active {
    background: #eff2f7;
    width: 30px;
    border-radius: 6px;
  }
  .swiper-next, .swiper-prev {
    position: absolute;
    top: 50%;
    z-index: 10;
    margin-top: -37.5px;
    cursor: pointer;
    transition: all .3s;
    &:hover {
      background: rgba(0, 0, 0, .3);
    }
  }
  .icon-left, .icon-right {
    font-size: 50px;
    color: #fff;
  }
  .swiper-next {
    right: -100px;
  }
  .swiper-prev {
    left: -100px;
  }
  &:hover {
    .swiper-next {
      right: 20px;
    }
    .swiper-prev {
      left: 20px;
    }
  }
}