.archive {
  background-color: #fff;
  box-sizing: border-box;
  padding: 20px;
  h1 {
    text-align: center;
  }
  h3 {
    margin-top: 1em;
  }
}

@media all and (max-width: 960px) {
  .archive {
    padding-left: 0;
    padding-right: 0;
  }
  .archive h3 {
    padding-left: 15px;
    line-height: 1;
  }
}