// 普通列表
.log-list-item {
  position: relative;
  padding: $padding;
  margin-bottom: $separation;
  background: #fff;
  @include box-shadow-normal;
  .cover-image {
    display: block;
    overflow: hidden;
    border-radius: 4px;
    font-size: 0;
    img {
      width: 100%;
    }
  }
  &.log-list-image-1 {
    display: flex;
    align-items: stretch;
    min-height: 140px;
    .cover-image {
      width: 200px;
      height: 150px;
      margin-right: $padding;
    }
    .log-list-content {
      flex: 1;
    }
    .description {
      min-height: 80px;
    }
  }
  // 2-3个图片时
  &.log-list-images {
    .log-images {
      display: flex;
      margin-top: 1em;
      flex-wrap: wrap;
      justify-content: space-between;
    }
    .cover-image {
      width: 32%;
      height: 150px;
    }
    .log-images-2 .cover-image {
      width: 49.5%;
    }
  }
  .title h3 {
    font-size: 21px;
    margin-bottom: 0.4em;
  }
  .description {
    color: $color-text-2;
    line-height: 1.8;
    img {
      display: none;
    }
  }
  .info {
    font-size: 13px;
    color: $color-text-3;
    margin-bottom: 1em;
    a {
      font-weight: bold;
    }
    span, a {
      margin-right: 10px;
      display: inline-block;
    }
    .view {
      float: right;
    }
  }
  .badge {
    position: relative;
    bottom: 3px;
  }
}

// 单列列表
.log-list_single {
  .log-list-item {
    min-height: inherit;
    padding-left: 15px;
    .log-list-image {
      position: static;
      width: 100%;
      height: 200px;
      margin-bottom: 20px;
      &:hover img {
        width: 100%;
      }
    }
  }
}

// 双列列表
.log-list_2 {
  .log-list-item {
    float: left;
    width: 49.5%;
    box-sizing: border-box;
    &:nth-of-type(2n-1) {
      margin-right: 1%;
    }
    .title {
      min-height: 48px;
    }
  }
}

// 三列列表
.log-list_three {
  .log-list-item {
    min-height: auto;
    padding-left: 120px;
    .title {
      font-size: 18px;
    }
    .info a {
      font-weight: normal;
    }
  }
  .x-info {
    position: absolute;
    left: 0;
    top: 18px;
    width: 110px;
  }
}

// 图片形式列表
.log-list_img, .log-list_mv {
  font-size: 0;
  .log-list-item {
    font-size: 14px;
    display: inline-block;
    width: 25%;
    box-sizing: border-box;
    padding: 0;
    margin-bottom: 10px;
    vertical-align: bottom;
    padding-right: 10px;
    &:nth-of-type(4n) {
      padding-right: 0;
    }
  }
  .img-link {
    display: block;
    position: relative;
    height: 180px;
    overflow: hidden;
    border-radius: 4px;
    img {
      width: 100%;
    }
    &:hover {
      transition: all .5s;
      opacity: .8;
    }
  }
  .info {
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    .date {
      float: none;
      position: absolute;
      right: 10px;
      margin-right: 0;
    }
  }
}
.log-list_mv .info {
  padding: 0 53.69px 0 0;
}
.log-list_img .info {
  padding: 0 102px 0 0;
}

// 视频形式列表
.log-list_mv {
  .play {
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .2);
    text-align: center;
    transition: all .5s;
    .iconfont {
      position: absolute;
      top: 50%;
      left: 50%;
      margin-top: -25px;
      margin-left: -20px;
      color: #fff;
      font-size: 50px;
      line-height: 1;
    }
  }
  .log-list-item:hover .play,
  .log-list-item.active .play {
    display: block;
    background-color: rgba(0, 0, 0, .5);
  }
}

// ias分页
.log-loading {
  text-align: center;
  padding: 15px 0;
}
.x-info {
  overflow: hidden;
  .item {
    width: 55px;
    float: left;
    text-align: center;
  }
  span {
    display: block;
    font-weight: bold;
  }
}

.log-list_text {
  padding: 0 15px;
  height: 32px;
  line-height: 32px;
  list-style-position: inside;
  list-style-type: circle;
}
.log-list_text:nth-child(2n) {
  background-color: #f9f9f9;
}
.log-list_text .date {
  float: right;
  font-family: serif;
}
.log-list_text_title {
  padding: 10px 0;
  font-weight: bold;
  font-size: 30px;
}